/**
 * クラス名を持つかどうか調べる
 *
 * @param {string} nowClass
 * @param {string} checkClass
 * @return {boolean} 有無
 */
export default function hasClass(nowClass, checkClass) {
	// クラスを配列化 : 完全一致させるため
	const nowClasses = nowClass.split(' ');

	// 対象のクラスを所持しているかどうかチェック。
	return -1 !== nowClasses.indexOf(checkClass);
}
